#pub-top{
    padding-top: 1em;
    padding-bottom: 1em;
    text-align: center;
}
#pub-title{font-weight:bold; font-size:2em;}
#pub-text{font-size:1.2em;}


#pub-kind-btn{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1em;
    margin-bottom: 3em;
}
#pub-kind-btn button{
    width: 7em;
    height: 2em;
    margin-left: 3em;
    margin-right: 3em;
    font-size: 1.3em;
    font-style:inherit;
    border: 0;
    border-radius: 20px;
    background: none;
    cursor: pointer;
    outline: none;
    transition: 0.2s;
}
#pub-kind-btn button:hover{color:white; background:black;}

@media screen and (max-width : 750px){
    #pub-kind-btn{flex-direction: column;}
}


#pub-per{
    width: 52em;
    margin: auto;
    margin-bottom: 5em;
    padding: 0 1em;
    border: 2px solid black;
    border-radius: 30px;
    transition: 0.4s;
}
#per-jour{margin-bottom:-0.2em; font-size:1.2em; font-weight:700;}
#per-year{color:gray; font-size:0.8em;}
#per-doi{width:4.5em; height:2.2em; margin-left:1em; font-size:0.6em; text-align:right; border:1px solid rgb(184, 183, 183); background:none;}
#per-doi img{width:1.5em; height:1.5em; vertical-align:middle;}
#per-doi:hover{border:1px solid red;}