.nav{
    width: 100%;
    height: 5em;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #022601;
    z-index: 1000;
}

#nav-menu{
    height: 2em;
    width: 6em;
    margin: 0 2em;
    font-size: 14px;
    color: white;
    background: none;
    border: 0;
    border-radius: 12px;
    cursor: pointer;
    outline: none;
    transition: 0.3s;
}

#nav-menu:hover{
    color: black;
    background: white;
}


@media screen and (max-width : 800px){
    .nav{
        height: 13em;
        flex-direction: column;
    }
    #nav-menu{
        width: 100%;
        height: 2.5em;
    }
}