.people{
    width: 100%;
}

#people-top{
    padding-top: 1em;
    padding-bottom: 1em;
    text-align: center;
}
#people-title{font-weight:bold; font-size:2em;}
#people-text{font-size:1.2em;}

#people-member{
    padding-top: 1em;
    padding-bottom: 1em;
    text-align: center;
}

#people-section{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
#people-member-title{font-weight:bold; font-size:1.8em;}

#people-member-info{
    width: 250px;
    height: 320px;
    margin-bottom: 4em;
    margin-left: 4em;
    margin-right: 4em;
    text-align: left;
    box-shadow: 5px 5px 10px gray;
    background: white;
}
#member-name{margin-left:1em; line-height:1em; font-weight: bold; font-size:1.1em;}
#member-info{margin-left:1em; line-height:0.6em; font-size:0.9em;}
#people-member-info p{margin-left:1em; line-height:0.6em;}

#member-image{width:250px; height:200px; border-bottom:1px solid black;}