#res-top{
    padding-top: 1em;
    padding-bottom: 1em;
    text-align: center;
}
#res-title{font-weight:bold; font-size:2em;}
#res-text{font-size:1.2em;}

#res-section{
    width: 60%;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

#res-content{
    width: 17em;
    height: 15em;
    line-height: 15em;
    perspective: 1500px;
}
#res-content:hover #res-content-box{transform: rotateY(180deg);}
#res-content-box{
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transform: rotateY(0deg);
    transition: 0.4s;
}
#res-content-img, #res-content-sub{
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    text-align: center;
    font-size: 2em;
    color: white;
}
#res-content-sub{transform: rotateY(180deg);}
#res-content-img{background: rgb(116, 184, 163);}
#res-content-sub{background: rgb(80, 97, 143);}
